import React from 'react';
import cx from 'classnames';
import ArchiveCover from '../ArchiveCover/ArchiveCover';
import ToggleButton from '../ToggleButton';
import s from './SidebarArchives.scss';
import {ArchiveObjectContainer} from "../../types";


interface Props {
  archives: ArchiveObjectContainer;
  displayQueue: boolean;
  toggleQueue: () => void;
  className?: string;
}

const SidebarArchives = ({displayQueue, archives, toggleQueue, className}: Props) => (
  <div className={cx(className, displayQueue ? s.archCont : null)}>
    {
      displayQueue ? archives.objects.map(
        (archive) => (
          <div key={archive.id}>
            <ArchiveCover
              archive={archive}
            />
          </div>
        ),
      ) : null
    }
    <ToggleButton onClick={toggleQueue} showContent={displayQueue} alignRight={false}/>
  </div>
);

export default SidebarArchives;