import React, { Component } from 'react';
import {
  Route,
  withRouter,
} from 'react-router-dom';
import { HotKeys } from 'react-hotkeys';
import cx from 'classnames';
import Archive from './Archive';
import Help from './Help';
import SidebarArchives from './SidebarArchives';
import SidebarControls from './SidebarControls';
import { getArchivesComponent } from '../core/apiCalls';
import s from './Home.scss';
import {ArchiveObjectContainer, GenericHTMLDivElement, Match} from "../types";

interface Props {
  match: Match;
  location: Location;
  history: {
    push: (url: string) => void;
  };
}

interface State {
  archives: ArchiveObjectContainer | null;
  displayHelp: boolean;
  displayControls: boolean;
  displayQueue: boolean;
  displayCovers: boolean;
  displayNavigators: boolean;
  displayMetadata: boolean;
  displayPreloadInfo: boolean;
  doublePageMode: boolean;
  autoWidth: boolean;
  scale: number;
  currentContainer: GenericHTMLDivElement | null;
}

class Home extends Component<Props, State> {

  state = {
    archives: null,
    displayQueue: false,
    displayHelp: false,
    displayControls: false,
    currentArchive: null,
    displayCovers: false,
    displayNavigators: true,
    displayMetadata: false,
    displayPreloadInfo: false,
    doublePageMode: false,
    autoWidth: false,
    scale: 1.0,
    currentContainer: null,
  };

  _HotKeysContainer: React.RefObject<HTMLElement>;

  constructor(props: Props) {
    super(props);
    this._HotKeysContainer = React.createRef<HTMLElement>();

  }

  componentDidMount() {
    // window.addEventListener('keydown', this.handleKeyDown);
    if(this._HotKeysContainer && this._HotKeysContainer.current) {
      this._HotKeysContainer.current.focus();
    }
    getArchivesComponent(this.props.location.search).then(archives => {
      this.setState({ archives });
    });
  };

  componentDidUpdate(prevProps: Props) {
    const oldSearch = prevProps.location.search;
    const newSearch = this.props.location.search;
    if (newSearch !== oldSearch)
      getArchivesComponent(newSearch).then(archives => {
        this.setState({ archives });
      })
  };

  componentWillUnmount() {
    // window.removeEventListener('keydown', this.handleKeyDown);
  }

  toggleFullSize = () => {
    this.setState({ autoWidth: !this.state.autoWidth });
  };

  toggleMetadata = () => {
    this.setState({ displayMetadata: !this.state.displayMetadata });
  };

  toggleDoublePageMode = () => {
    this.setState({ doublePageMode: !this.state.doublePageMode });
  };

  decreaseScale = () => {
    this.setState({ scale: this.state.scale - 0.1 });
  };

  increaseScale = () => {
    this.setState({ scale: this.state.scale + 0.1 });
  };

  resetScale = () => {
    this.setState({ scale: 1.0 });
  };

  togglePreloadInfo = () => {
    this.setState({ displayPreloadInfo: !this.state.displayPreloadInfo });
  };

  toggleHelp = () => {
    this.setState({ displayHelp: !this.state.displayHelp });
  };

  toggleCovers = () => {
    this.setState({ displayCovers: !this.state.displayCovers });
  };

  toggleQueue = () => {
    this.setState({ displayQueue: !this.state.displayQueue });
  };

  toggleControls = () => {
    this.setState({ displayControls: !this.state.displayControls });
  };

  setCurrentContainer = (container: GenericHTMLDivElement | null) => {
    this.setState({ currentContainer: container });
  };

  contentFullScreen = () => {
    if (!(document).fullscreenElement &&    // alternative standard method
      !(document).mozFullScreenElement && !(document).webkitFullscreenElement && !(document).msFullscreenElement) {
      const elem = this.state.currentContainer;
      if (elem) {
        if (elem.requestFullscreen) {
          elem.requestFullscreen();
        } else if (elem.msRequestFullscreen) {
          elem.msRequestFullscreen();
        } else if (elem.mozRequestFullScreen) {
          elem.mozRequestFullScreen();
        } else if (elem.webkitRequestFullscreen) {
          elem.webkitRequestFullscreen();
        }
      }
    } else if ((document).exitFullscreen) {
      (document).exitFullscreen();
    } else if ((document).msExitFullscreen) {
      (document).msExitFullscreen();
    } else if ((document).mozCancelFullScreen) {
      (document).mozCancelFullScreen();
    } else if ((document).webkitExitFullscreen) {
      (document).webkitExitFullscreen();
    }
  };

  actions: {key: string; name: string; description: string; action?: Function}[] = [
    {key: 'left, a', name: 'moveLeft', description: 'Move Left'},
    {key: 'right, d', name: 'moveRight', description: 'Move Right'},
    {key: 'shift+left', name: 'moveFirst', description: 'Move First'},
    {key: 'shift+right', name: 'moveLast', description: 'Move Last'},
    {key: 'y', name: 'previousQueue', description: 'Previous in queue'},
    {key: 'i', name: 'nextQueue', description: 'Next in queue'},
    {key: 'h', name: 'toggleHelp', description: 'Toggle help'},
    {key: 'p', name: 'toggleQueue', description: 'Toggle queue'},
    {key: 'l', name: 'toggleControls', description: 'Toggle controls'},
    {key: '-', name: 'decreaseScale', description: 'Decrease Scale', action: this.decreaseScale},
    {key: '+', name: 'increaseScale', description: 'Increase Scale', action: this.increaseScale},
    {key: '=', name: 'resetScale', description: 'Reset Scale', action: this.resetScale},
    {key: 'g', name: 'toggleFullSize', description: 'Full Size', action: this.toggleFullSize},
    {key: 'f', name: 'toggleFullScreen', description: 'Full Screen', action: this.contentFullScreen},
    {key: 'c', name: 'toggleCovers', description: 'Covers'},
    {key: 't', name: 'toggleDoublePageMode', description: 'Double page mode', action: this.toggleDoublePageMode},
    {key: 'u', name: 'togglePositionModal', description: 'Position Modal'},
    {key: 'j', name: 'toggleMetadata', description: 'Metadata', action: this.toggleMetadata},
    {key: 'shift+j', name: 'togglePreloadInfo', description: 'Preload Info', action: this.togglePreloadInfo}
  ];

  // Be careful not to clash keys with ImageViewer.
  keyHandlers = {
    'togglePreloadInfo': this.togglePreloadInfo,
    'decreaseScale': this.decreaseScale,
    'increaseScale': this.increaseScale,
    'resetScale': this.resetScale,
    'toggleFullSize': this.toggleFullSize,
    'toggleCovers': this.toggleCovers,
    'toggleDoublePageMode': this.toggleDoublePageMode,
    'toggleHelp': this.toggleHelp,
    'toggleMetadata': this.toggleMetadata,
    'toggleQueue': this.toggleQueue,
    'toggleControls': this.toggleControls,
    'toggleFullScreen': this.contentFullScreen,
  };

  handleQueueChange = (archiveId: string, queueChange: number) => {
    if (this.state.archives) {
      const foundIndex = this.state.archives.objects.findIndex((archive) => archive.id === archiveId);
      if (foundIndex > -1 && this.state.archives) {
        const probableNextPosition = foundIndex + queueChange;
        if (probableNextPosition >= 0 && probableNextPosition < this.state.archives.objects.length) {
          this.props.history.push(`/${this.state.archives.objects[probableNextPosition].id}/img/1${this.props.location.search}`);
        }
      }
    }
  };

  render() {
    const {
      archives, displayQueue, displayHelp,
      displayCovers, displayNavigators,
      displayMetadata, autoWidth,
      displayPreloadInfo, doublePageMode,
      displayControls, scale
    } = this.state;
    const { match } = this.props;
    return (
      // @ts-ignore
      <HotKeys
        className={s.HotKeys}
        innerRef={this._HotKeysContainer}
        keyMap={
          this.actions
            .filter(obj => this.keyHandlers.hasOwnProperty(obj.name))
            .reduce((map, obj) => {map[obj.name] = obj.key; return map;}, {})
        }
        handlers={this.keyHandlers}>
        <div>
          {
            <div className={s.container}>
              {
                archives ?
                  <SidebarArchives
                    className={s.overlayLeft}
                    archives={archives}
                    displayQueue={displayQueue}
                    toggleQueue={this.toggleQueue}
                  /> : null
              }
              <div style={{ position: 'relative' }}>
                <Route
                  path={`${match.url}:archiveId/img/:imgId`}
                  render={ props => <Archive
                    handleQueueChange={this.handleQueueChange}
                    displayCovers={displayCovers}
                    displayNavigators={displayNavigators}
                    displayMetadata={displayMetadata}
                    displayPreloadInfo={displayPreloadInfo}
                    doublePageMode={doublePageMode}
                    setCurrentContainer={this.setCurrentContainer}
                    autoWidth={autoWidth}
                    scale={scale}
                    {...props}
                  />}
                />
              </div>
              {
                displayHelp && <div className={cx(s.overlay, s.overlayHelp)}><Help actions={this.actions}/></div>
              }
              <SidebarControls
                className={s.overlayRight}
                displayControls={displayControls}
                toggleControls={this.toggleControls}
                controls={this.actions.filter(x => x.action !== undefined)}
              />
            </div>
          }
        </div>
      </HotKeys>
    );
  }

}

export default withRouter(Home);